.container {
  width: 100%;
  height: 96px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  position: sticky;
  top: 0;
  background-color: #fff;
}

.container_first, .container_second {
  width: 50%;
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 24px;
}

.container_first_child ,.container_second_child {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin: 0;
  padding: 0;
}

.container_second_child {
  flex-direction: row-reverse;
}

.container_first_child_logo {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  width: 20%;
  height: 100%;
  padding: 0;
  margin: 0;
  user-select: none;
}

.container_second_child_navgation {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center; 
}

.container_second_child_navgation a {
  position: relative;
  /*width: 25%;*/
  height: 100%;
  text-decoration: none;
  color: black;
  display: flex;
  padding: 0 5px;
  box-sizing: border-box;
  justify-content: center;
  user-select: none;
  align-items: center;
  transition: .15s;
  -webkit-transition: .15s;
  -moz-transition: .15s;
  -ms-transition: .15s;
  -o-transition: .15s;
}

.container_second_child_navgation a:hover {
  transform: translateY(-10%);
  color: red;
  -webkit-transform: translateY(-10%);
  -moz-transform: translateY(-10%);
  -ms-transform: translateY(-10%);
  -o-transform: translateY(-10%);
}

/* FORM BUTTONS */
/* SIGN IN */
.container_second_child_signIn, .container_second_child_signUp {
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  cursor: pointer;
}

.container_second_child_signIn {
  order: 1;
  color: lightcoral;
}

.container_second_child_signIn:hover {
  background-color: rgba(206, 206, 207, 0.911);
  user-select: none;
  text-decoration: none;
  color: #fff;
}

.container_second_child_signIn:active {
  background-color: rgba(255, 255, 255, 0.562);
}

.container_second_child_signIn a span {
  user-select: none;
  text-decoration: none;
}

/* SIGN UP */
.container_second_child_signUp {
  background-color: lightcoral;
  color: #fff;
}

.container_second_child_signUp a span {
  color: white;
  user-select: none;
  text-decoration: none;
}

.container_second_child_signUp:hover {
    background-color: rgb(250, 51, 81);
    user-select: none;
    text-decoration: none;
    color: #fff;
}


.container_second_child_signUp:active {
  background-color: white;
}

/* LUPA */
.container_second_child_lupa {
  width: 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  order: 2;
}

.container_second_child_lupa:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}